const defaultState:any = {
    text: 'initial',
    foo: {
      bar: 'zoo',
      nested: {
        veryDeep: true,
      },
    },
  };
  
  export default function(state=defaultState, action:any = {}) {
    switch(action.type) {
      case 'UPDATE':
        return {
          ...state,
          text: action.text,
          foo: {
            ...state.foo,
            bar: action.text,
          },
        };
        case 'UPLOAD':
          return {
            ...state,
            upload_data: action
          };
      default:
        return state;
    }
  }