import axios from 'axios'
import {AuthModel, UserModel} from './_models'

const API_URL = process.env.REACT_APP_API_URL

const GET_USER_BY_ACCESSTOKEN_URL:any = process.env.REACT_APP_GET_USER_BY_ACCESSTOKEN_URL
const OTP_VALIDATE:any = process.env.REACT_APP_OTP_VALIDATE
const LOGIN_URL :any = process.env.REACT_APP_LOGIN_URL
const REGISTER_URL:any = process.env.REACT_APP_REGISTER_URL
const REQUEST_PASSWORD_URL:any = process.env.REACT_APP_REQUEST_PASSWORD_URL
const GetBoOption_URL:any = process.env.REACT_APP_GetBoOption_URL
const GetBoTYPES_URL:any = process.env.REACT_APP_GetBoTYPES_URL
const GetBoResidencies_URL:any = process.env.REACT_APP_GetBoResidencies_URL
const GetBoDPs_URL:any = process.env.REACT_APP_GetBoDPs_URL
const CountryList:any = process.env.REACT_APP_CountryList
const statementsList:any = process.env.REACT_APP_statementsList
const bo_ac_create:any = process.env.REACT_APP_bo_ac_create
const bo_list:any = process.env.REACT_APP_bo_list  
const trade_list:any = process.env.REACT_APP_trade_list 
const REACT_APP_TRADE_SELL:any = process.env.REACT_APP_TRADE_SELL
const REACT_APP_TRADE_BUY:any = process.env.REACT_APP_TRADE_BUY

// Server should return AuthModel
export function login(user_name: any, password: any) {
  return axios.post<any>(LOGIN_URL, {
    user_name,
    password,
  })
}

export function DataFormateForSelect(data:any , val:string, lab:string ) {
  return data.map((collection:any,key:any)=>{
    let datas:any = {}
    datas.value = collection[val]
    datas.label = collection[lab]
    return datas
  })
}


// Server should return AuthModel
export function register(
  email: string,
  username: string,
  password: string
) {
  return axios.post(REGISTER_URL, {
    email,
    username,
    password
  })
}

// Server should return object => { result: boolean } (Is Email in DB)
export function requestPassword(email: string) {
  return axios.post<{result: boolean}>(REQUEST_PASSWORD_URL, {
    email,
  })
}

export function otpValidation(email: string , otp:string) {
  return axios.post(OTP_VALIDATE, {
    email,
    otp,
    reason:"signup",
  })
}

export function getUserByToken(token: string) {
  return axios.post<UserModel>(GET_USER_BY_ACCESSTOKEN_URL, {
    api_token: token,
  })
}





export function GetBoOption() {
  return axios.get(GetBoOption_URL)
}

export function GetBoType() {
  return axios.get(GetBoTYPES_URL)
}

export function GetBoResidence() {
  return axios.get(GetBoResidencies_URL)
}

export function GetBoDps() {
  return axios.get(GetBoDPs_URL)
}

export function GetBoDpsDetails(id:number) {
  return axios.get(GetBoDPs_URL+"/"+id)
}

export function GetCountryList() {
  return axios.get(CountryList)
}

export function GetStatementCycle() {
  return axios.get(statementsList)
}

export function bo_account_Create(data:any) {
  return axios.post<any>(bo_ac_create, {
    ...data
  })
}

export function BoList() {
  return axios.get(bo_list)
}

export function tradeList() {
  return axios.get(trade_list)
}

export function tradeSell(data:any) {
  return axios.post<any>(REACT_APP_TRADE_SELL, {
    ...data
  })
}

export function tradeBuy(data:any) {
  return axios.post<any>(REACT_APP_TRADE_BUY, {
    ...data
  })
}

export function trade_details_info(id:any) {
  return axios.get(process.env.REACT_APP_TRADE_COMPANY+id)
}

export function UserTradeList(id:any) {
  return axios.get(process.env.REACT_APP_ALL_BUY_SELL_BY_USER+id)
}

export function ShareList(type:any) {
  let url :string = window.location.protocol + "//" + window.location.host + "/trading.json"
  return axios.get(url)
}

export function IndicesList() {
  let url :string = window.location.protocol + "//" + window.location.host + "/indices.json"
  return axios.get(url)
}

export function eventCalanderList() {
  let url :string = window.location.protocol + "//" + window.location.host + "/calanderEvents.json"
  return axios.get(url)
}